"use client"

import { useQuery } from "@tanstack/react-query"
import Link from "next/link"
import { 
  UserCheck, 
  Truck, 
  Package, 
  TrendingUp,
  AlertCircle,
  Clock,
  CheckCircle,
  ArrowRight,
  ChevronRight
} from "lucide-react"
import { formatPaise } from "@/lib/constants"
import { formatDateTime } from "@/lib/utils"

// Loading Skeleton
function DashboardSkeleton() {
  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-8">
        {/* Header Skeleton */}
        <div>
          <div className="h-8 w-48 bg-gray-200 rounded-lg animate-pulse" />
          <div className="h-4 w-72 bg-gray-200 rounded mt-2 animate-pulse" />
        </div>
        
        {/* KPI Cards Skeleton */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {[1, 2, 3, 4].map(i => (
            <div key={i} className="h-32 bg-white rounded-2xl animate-pulse" />
          ))}
        </div>
        
        {/* Two Column Skeleton */}
        <div className="grid lg:grid-cols-2 gap-6">
          <div className="h-80 bg-white rounded-2xl animate-pulse" />
          <div className="h-80 bg-white rounded-2xl animate-pulse" />
        </div>
      </div>
    </div>
  )
}

export default function AdminDashboard() {
  const { data, isLoading, error } = useQuery({
    queryKey: ["admin-dashboard"],
    queryFn: async () => {
      const res = await fetch("/api/admin/dashboard")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
    refetchInterval: 60000,
  })

  if (isLoading) return <DashboardSkeleton />

  if (error) {
    return (
      <div className="p-6 lg:p-8">
        <div className="max-w-7xl mx-auto">
          <div className="bg-red-50 border border-red-200 rounded-2xl p-6">
            <div className="flex items-center gap-3 text-red-700">
              <AlertCircle className="w-6 h-6 flex-shrink-0" />
              <div>
                <p className="font-semibold">Failed to load dashboard</p>
                <p className="text-sm text-red-600">Please refresh the page to try again.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

  const { overview, todayStats, inventory, todayRevenue, recentTransactions } = data.data

  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-8">
        
        {/* ====== PAGE HEADER ====== */}
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-500 mt-1">Welcome back! Here's what's happening today.</p>
        </div>

        {/* ====== PENDING APPROVALS ALERT ====== */}
        {overview.pendingApprovals > 0 && (
          <Link href="/admin/customers?status=PENDING_APPROVAL" className="block">
            <div className="bg-amber-50 border border-amber-200 rounded-2xl p-5 hover:bg-amber-100 transition-colors cursor-pointer">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-4">
                  <div className="w-12 h-12 bg-amber-200 rounded-xl flex items-center justify-center flex-shrink-0">
                    <Clock className="w-6 h-6 text-amber-700" />
                  </div>
                  <div>
                    <p className="text-lg font-semibold text-amber-800">
                      {overview.pendingApprovals} Customer{overview.pendingApprovals > 1 ? 's' : ''} Pending Approval
                    </p>
                    <p className="text-sm text-amber-600">New registrations waiting for your review</p>
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  <span className="inline-flex items-center justify-center px-3 py-1 bg-amber-200 text-amber-800 text-sm font-bold rounded-full">
                    {overview.pendingApprovals}
                  </span>
                  <ArrowRight className="w-5 h-5 text-amber-600" />
                </div>
              </div>
            </div>
          </Link>
        )}

        {/* ====== KPI CARDS ====== */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {/* Active Customers */}
          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">Active Customers</p>
                <p className="text-4xl font-bold text-gray-900">{overview.activeCustomers}</p>
              </div>
              <div className="w-14 h-14 bg-emerald-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <UserCheck className="w-7 h-7 text-emerald-600" />
              </div>
            </div>
          </div>

          {/* Delivery Team */}
          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">Delivery Team</p>
                <p className="text-4xl font-bold text-gray-900">{overview.activeDeliveryPersons}</p>
              </div>
              <div className="w-14 h-14 bg-blue-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <Truck className="w-7 h-7 text-blue-600" />
              </div>
            </div>
          </div>

          {/* Bottles Out */}
          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">Bottles Out</p>
                <p className="text-4xl font-bold text-gray-900">
                  {inventory.inCirculationLarge + inventory.inCirculationSmall}
                </p>
              </div>
              <div className="w-14 h-14 bg-purple-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <Package className="w-7 h-7 text-purple-600" />
              </div>
            </div>
          </div>

          {/* Today's Revenue */}
          <div className="bg-white rounded-2xl p-6 border border-gray-200 shadow-sm">
            <div className="flex items-start justify-between">
              <div>
                <p className="text-sm font-medium text-gray-500 mb-1">Today's Revenue</p>
                <p className="text-4xl font-bold text-gray-900">{formatPaise(todayRevenue)}</p>
              </div>
              <div className="w-14 h-14 bg-emerald-100 rounded-2xl flex items-center justify-center flex-shrink-0">
                <TrendingUp className="w-7 h-7 text-emerald-600" />
              </div>
            </div>
          </div>
        </div>

        {/* ====== TWO COLUMN SECTION ====== */}
        <div className="grid lg:grid-cols-2 gap-6">
          
          {/* Today's Deliveries Card */}
          <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
            <div className="px-6 py-5 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">Today's Deliveries</h2>
            </div>
            <div className="p-6 space-y-4">
              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                <span className="text-gray-600 font-medium">Total Scheduled</span>
                <span className="text-2xl font-bold text-gray-900">{todayStats.total}</span>
              </div>
              
              <div className="flex items-center justify-between p-4 bg-emerald-50 rounded-xl">
                <div className="flex items-center gap-3">
                  <CheckCircle className="w-5 h-5 text-emerald-600" />
                  <span className="text-emerald-700 font-medium">Delivered</span>
                </div>
                <span className="text-2xl font-bold text-emerald-700">{todayStats.delivered}</span>
              </div>
              
              <div className="flex items-center justify-between p-4 bg-amber-50 rounded-xl">
                <div className="flex items-center gap-3">
                  <Clock className="w-5 h-5 text-amber-600" />
                  <span className="text-amber-700 font-medium">Pending</span>
                </div>
                <span className="text-2xl font-bold text-amber-700">{todayStats.scheduled}</span>
              </div>
              
              <div className="flex items-center justify-between p-4 bg-red-50 rounded-xl">
                <div className="flex items-center gap-3">
                  <AlertCircle className="w-5 h-5 text-red-600" />
                  <span className="text-red-700 font-medium">Not Delivered / Paused</span>
                </div>
                <span className="text-2xl font-bold text-red-700">
                  {todayStats.notDelivered + todayStats.paused}
                </span>
              </div>
            </div>
          </div>

          {/* Inventory Status Card */}
          <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
            <div className="px-6 py-5 border-b border-gray-200">
              <h2 className="text-lg font-semibold text-gray-900">Inventory Status</h2>
            </div>
            <div className="p-6 space-y-6">
              {/* 1L Bottles */}
              <div>
                <div className="flex items-center justify-between mb-3">
                  <span className="font-medium text-gray-700">1L Bottles</span>
                  <span className="text-sm text-gray-500">
                    <span className="font-semibold text-gray-900">{inventory.availableLarge}</span> available / {inventory.totalLarge} total
                  </span>
                </div>
                <div className="h-3 bg-gray-100 rounded-full overflow-hidden">
                  <div 
                    className="h-full bg-emerald-500 rounded-full transition-all duration-500"
                    style={{ 
                      width: `${inventory.totalLarge > 0 
                        ? (inventory.inCirculationLarge / inventory.totalLarge) * 100 
                        : 0}%` 
                    }}
                  />
                </div>
                <p className="text-sm text-gray-500 mt-2">
                  {inventory.inCirculationLarge} with customers
                </p>
              </div>

              {/* 500ml Bottles */}
              <div>
                <div className="flex items-center justify-between mb-3">
                  <span className="font-medium text-gray-700">500ml Bottles</span>
                  <span className="text-sm text-gray-500">
                    <span className="font-semibold text-gray-900">{inventory.availableSmall}</span> available / {inventory.totalSmall} total
                  </span>
                </div>
                <div className="h-3 bg-gray-100 rounded-full overflow-hidden">
                  <div 
                    className="h-full bg-amber-500 rounded-full transition-all duration-500"
                    style={{ 
                      width: `${inventory.totalSmall > 0 
                        ? (inventory.inCirculationSmall / inventory.totalSmall) * 100 
                        : 0}%` 
                    }}
                  />
                </div>
                <p className="text-sm text-gray-500 mt-2">
                  {inventory.inCirculationSmall} with customers
                </p>
              </div>

              {/* Link */}
              <Link 
                href="/admin/inventory"
                className="flex items-center justify-center gap-2 w-full py-3 text-sm font-semibold text-emerald-600 hover:text-emerald-700 hover:bg-emerald-50 rounded-xl transition-colors"
              >
                Manage Inventory
                <ChevronRight className="w-4 h-4" />
              </Link>
            </div>
          </div>
        </div>

        {/* ====== RECENT TRANSACTIONS ====== */}
        <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
          <div className="px-6 py-5 border-b border-gray-200">
            <h2 className="text-lg font-semibold text-gray-900">Recent Transactions</h2>
          </div>
          
          {recentTransactions.length === 0 ? (
            <div className="p-12 text-center">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <TrendingUp className="w-8 h-8 text-gray-400" />
              </div>
              <p className="text-gray-500 font-medium">No recent transactions</p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b border-gray-200 bg-gray-50">
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-600">Customer</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-600">Type</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-600">Amount</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-600 hidden md:table-cell">Date</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {recentTransactions.slice(0, 10).map((tx: any) => (
                    <tr key={tx.id} className="hover:bg-gray-50 transition-colors">
                      <td className="px-6 py-4">
                        <span className="font-medium text-gray-900">
                          {tx.wallet?.customer?.name || "Unknown"}
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <span className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-semibold ${
                          tx.type === "WALLET_TOPUP" 
                            ? "bg-emerald-100 text-emerald-800"
                            : tx.type === "PENALTY_CHARGE"
                            ? "bg-red-100 text-red-800"
                            : "bg-gray-100 text-gray-800"
                        }`}>
                          {tx.type.replace(/_/g, " ")}
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <span className={`font-semibold ${
                          tx.amountPaise > 0 ? "text-emerald-600" : "text-red-600"
                        }`}>
                          {tx.amountPaise > 0 ? "+" : ""}{formatPaise(tx.amountPaise)}
                        </span>
                      </td>
                      <td className="px-6 py-4 text-gray-500 hidden md:table-cell">
                        {formatDateTime(tx.createdAt)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>

      </div>
    </div>
  )
}
